"use client"

import { useState } from "react"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { BarChart, FileText, UserPlus, Vote, Info } from 'lucide-react'
import OverviewTab from "./tabs/overview-tab"
import ResultsTab from "./tabs/results-tab"
import VoteTab from "./tabs/vote-tab"
import RegisterTab from "./tabs/register-tab"

export default function CampaignTabs({
  campaignInfo,
  proposals,
  candidates,
  isConnected,
  userIsRegistered,
  userHasVoted,
  needsRegistration,
  isBeforeStart,
  isDuringVoting,
  isAfterEnd,
  canRegister,
  canVote,
  isOwner,
  address,
  campaignId,
  onUpdate
}) {
  const [activeTab, setActiveTab] = useState("overview")

  return (
    <Tabs defaultValue="overview" className="w-full" onValueChange={setActiveTab} value={activeTab}>
      <TabsList className="grid grid-cols-2 md:grid-cols-4 mb-6">
        <TabsTrigger value="overview" className="flex items-center gap-2">
          <Info className="h-4 w-4" />
          <span>Overview</span>
        </TabsTrigger>
        
        <TabsTrigger value="results" className="flex items-center gap-2">
          <BarChart className="h-4 w-4" />
          <span>Results</span>
        </TabsTrigger>
        
        {canVote && !isOwner && (
          <TabsTrigger value="vote" className="flex items-center gap-2">
            <Vote className="h-4 w-4" />
            <span>Vote</span>
          </TabsTrigger>
        )}
        
        {canRegister && !isOwner && (
          <TabsTrigger value="register" className="flex items-center gap-2">
            <UserPlus className="h-4 w-4" />
            <span>Register</span>
          </TabsTrigger>
        )}
      </TabsList>
      
      <TabsContent value="overview">
        <OverviewTab 
          campaignInfo={campaignInfo}
          isOwner={isOwner}
          isBeforeStart={isBeforeStart}
          isDuringVoting={isDuringVoting}
          isAfterEnd={isAfterEnd}
          proposals={proposals}
          candidates={candidates}
        />
      </TabsContent>
      
      <TabsContent value="results">
        <ResultsTab 
          campaignInfo={campaignInfo}
          proposals={proposals}
          candidates={candidates}
          isOwner={isOwner}
        />
      </TabsContent>
      
      {canVote && !isOwner && (
        <TabsContent value="vote">
          <VoteTab 
            campaignInfo={campaignInfo}
            proposals={proposals}
            candidates={candidates}
            isConnected={isConnected}
            userIsRegistered={userIsRegistered}
            userHasVoted={userHasVoted}
            needsRegistration={needsRegistration}
            address={address}
            onVote={onUpdate}
            campaignId={campaignId}
          />
        </TabsContent>
      )}
      
      {canRegister && !isOwner && (
        <TabsContent value="register">
          <RegisterTab 
            campaignInfo={campaignInfo}
            userIsRegistered={userIsRegistered}
            onRegister={onUpdate}
            campaignId={campaignId}
          />
        </TabsContent>
      )}
    </Tabs>
  )
}
