"use client"

import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { Info, Users, Award, BarChart2, Calendar, Clock, Shield } from 'lucide-react'
import { shortenAddress, formatDate, getVotingTypeText, getRestrictionText, getResultTypeText } from "@/lib/utils"

export default function OverviewTab({ 
  campaignInfo, 
  isOwner,
  isBeforeStart,
  isDuringVoting,
  isAfterEnd,
  proposals,
  candidates
}) {
  return (
    <div className="space-y-6">
      {/* Campaign Details */}
      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        <Card className="lg:col-span-2">
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <Info className="h-5 w-5" />
              Campaign Details
            </CardTitle>
            <CardDescription>
              Information about this voting campaign
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-6">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div className="space-y-4">
                <div>
                  <h3 className="text-sm font-medium text-muted-foreground mb-1">Description</h3>
                  <p className="font-medium">{campaignInfo.description}</p>
                </div>
                
                <div>
                  <h3 className="text-sm font-medium text-muted-foreground mb-1">Creator</h3>
                  <p className="font-medium">
                    {isOwner ? "You" : shortenAddress(campaignInfo.creator)}
                    {isOwner && <span className="ml-2 text-primary">(You)</span>}
                  </p>
                </div>
                
                <div>
                  <h3 className="text-sm font-medium text-muted-foreground mb-1">Campaign Type</h3>
                  <div className="flex flex-wrap gap-2">
                    <Badge variant="outline" className="bg-background">
                      {getVotingTypeText(campaignInfo.votingType)}
                    </Badge>
                    <Badge variant="outline" className="bg-background">
                      {getRestrictionText(campaignInfo.restriction)}
                    </Badge>
                    <Badge variant="outline" className="bg-background">
                      Results: {getResultTypeText(campaignInfo.resultType)}
                    </Badge>
                  </div>
                </div>
              </div>
              
              <div className="space-y-4">
                <div>
                  <h3 className="text-sm font-medium text-muted-foreground mb-1">Timeline</h3>
                  <div className="space-y-2">
                    <div className="flex items-center gap-2">
                      <Calendar className="h-4 w-4 text-muted-foreground" />
                      <span className="text-sm">Start: {formatDate(campaignInfo.startTime * 1000)}</span>
                    </div>
                    <div className="flex items-center gap-2">
                      <Calendar className="h-4 w-4 text-muted-foreground" />
                      <span className="text-sm">End: {formatDate(campaignInfo.endTime * 1000)}</span>
                    </div>
                  </div>
                </div>
                
                <div>
                  <h3 className="text-sm font-medium text-muted-foreground mb-1">Current Phase</h3>
                  <div className="flex items-center gap-2">
                    <Clock className="h-4 w-4 text-muted-foreground" />
                    <span className="text-sm">
                      {isBeforeStart ? "Registration Phase" : isDuringVoting ? "Voting Phase" : "Ended"}
                    </span>
                  </div>
                </div>
                
                <div>
                  <h3 className="text-sm font-medium text-muted-foreground mb-1">Access Control</h3>
                  <div className="flex items-center gap-2">
                    <Shield className="h-4 w-4 text-muted-foreground" />
                    <span className="text-sm">
                      {campaignInfo.restriction === 0 
                        ? "Open to all wallet addresses" 
                        : campaignInfo.restriction === 1 
                          ? `Limited to ${campaignInfo.maxVoters} voters` 
                          : "Registration with passkey required"}
                    </span>
                  </div>
                </div>
              </div>
            </div>
            
            {/* Phase-specific information */}
            {isBeforeStart && (
              <Alert className="border-amber-200 bg-amber-50 dark:bg-amber-950/20 dark:border-amber-900">
                <Info className="h-4 w-4 text-amber-600 dark:text-amber-400" />
                <AlertDescription className="text-amber-800 dark:text-amber-300 ml-2">
                  This campaign is in the registration phase. Voting will begin at the scheduled start time.
                </AlertDescription>
              </Alert>
            )}
            
            {isDuringVoting && (
              <Alert className="border-green-200 bg-green-50 dark:bg-green-950/20 dark:border-green-900">
                <Info className="h-4 w-4 text-green-600 dark:text-green-400" />
                <AlertDescription className="text-green-800 dark:text-green-300 ml-2">
                  Voting is currently active. Cast your vote before the campaign ends.
                </AlertDescription>
              </Alert>
            )}
            
            {isAfterEnd && (
              <Alert className="border-blue-200 bg-blue-50 dark:bg-blue-950/20 dark:border-blue-900">
                <Info className="h-4 w-4 text-blue-600 dark:text-blue-400" />
                <AlertDescription className="text-blue-800 dark:text-blue-300 ml-2">
                  This campaign has ended. Results are now available.
                </AlertDescription>
              </Alert>
            )}
          </CardContent>
        </Card>
        
        {/* Stats Card */}
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <BarChart2 className="h-5 w-5" />
              Campaign Stats
            </CardTitle>
            <CardDescription>
              Current statistics for this campaign
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-6">
            <div className="grid grid-cols-1 gap-4">
              <div className="bg-background p-4 rounded-lg flex flex-col items-center justify-center text-center">
                <Users className="h-8 w-8 text-primary mb-2" />
                <h3 className="text-lg font-medium">Registered Voters</h3>
                <p className="text-3xl font-bold">{campaignInfo.registeredVoterCount}</p>
              </div>
              
              <div className="bg-background p-4 rounded-lg flex flex-col items-center justify-center text-center">
                <Award className="h-8 w-8 text-primary mb-2" />
                <h3 className="text-lg font-medium">Total Votes</h3>
                <p className="text-3xl font-bold">{campaignInfo.totalVotes}</p>
              </div>
              
              <div className="bg-background p-4 rounded-lg flex flex-col items-center justify-center text-center">
                <BarChart2 className="h-8 w-8 text-primary mb-2" />
                <h3 className="text-lg font-medium">
                  {campaignInfo.votingType === 0 ? "Candidates" : "Proposals"}
                </h3>
                <p className="text-3xl font-bold">{campaignInfo.itemCount}</p>
              </div>
            </div>
            
            {campaignInfo.registeredVoterCount > 0 && (
              <div className="bg-background p-4 rounded-lg">
                <h3 className="font-medium mb-2">Participation Rate</h3>
                <div className="w-full bg-muted rounded-full h-2.5">
                  <div 
                    className="bg-primary h-2.5 rounded-full" 
                    style={{ 
                      width: `${Math.round((campaignInfo.totalVotes / campaignInfo.registeredVoterCount) * 100)}%` 
                    }}
                  ></div>
                </div>
                <p className="text-xs text-right mt-1 text-muted-foreground">
                  {Math.round((campaignInfo.totalVotes / campaignInfo.registeredVoterCount) * 100)}%
                </p>
              </div>
            )}
          </CardContent>
        </Card>
      </div>

      {/* Items Preview */}
      {(proposals.length > 0 || candidates.length > 0) && (
        <Card>
          <CardHeader>
            <CardTitle>
              {campaignInfo.votingType === 0 ? "Candidates" : "Proposals"} Preview
            </CardTitle>
            <CardDescription>
              {campaignInfo.votingType === 0 
                ? "Registered candidates for this campaign" 
                : "Available proposals for voting"}
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              {campaignInfo.votingType === 1 /* ProposalBased */ ? (
                proposals.slice(0, 4).map((proposal) => (
                  <div key={proposal.id} className="bg-background p-4 rounded-lg">
                    <h3 className="font-medium mb-1">Proposal {proposal.id + 1}</h3>
                    <p className="text-sm text-muted-foreground line-clamp-2">{proposal.content}</p>
                  </div>
                ))
              ) : (
                candidates.slice(0, 4).map((candidate) => (
                  <div key={candidate.id} className="bg-background p-4 rounded-lg">
                    <h3 className="font-medium mb-1">{candidate.name}</h3>
                    <p className="text-xs text-muted-foreground mb-1">
                      {shortenAddress(candidate.address)}
                    </p>
                    <p className="text-sm text-muted-foreground line-clamp-2">{candidate.statement}</p>
                  </div>
                ))
              )}
            </div>
            {((campaignInfo.votingType === 1 && proposals.length > 4) || 
              (campaignInfo.votingType === 0 && candidates.length > 4)) && (
              <p className="text-center text-sm text-muted-foreground mt-4">
                And {campaignInfo.votingType === 1 ? proposals.length - 4 : candidates.length - 4} more...
              </p>
            )}
          </CardContent>
        </Card>
      )}
    </div>
  )
}
